<template>
  <div>
    <el-dialog title="选择图片图片进行设计" :visible.sync="dialogTableVisible" @close="handleClose">
      <div>
        <el-input placeholder="请输入内容" v-model="searchImg" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="searchImgMethod"></el-button>
        </el-input>
        <el-select v-model="imgOptionVal" placeholder="请选择" style="margin: 10px 0;" @change="handleChangeImg">
          <el-option
            v-for="item in imgOption"
            :key="item.id"
            :label="item.label"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <el-checkbox-group v-model="checkedimagesList" @change="handleCheckedimagesListChange" class="textLeft">
        <el-row :gutter="12">
            <el-checkbox v-for="item in imagesList" :label="item" :key="item.id">
            <el-col>
              <el-card shadow="hover" class="imgInfo">
                <img v-lazy="item.path+'?imageView2/2/w/500'" alt="" srcset="">
              </el-card>
            </el-col>
          </el-checkbox>
        </el-row>
      </el-checkbox-group>
      <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :pager-count="pagerCount"
      layout="prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    <span slot="footer" class="dialog-footer">
      <el-button size="small" @click="handleClose">取 消</el-button>
      <el-button size="small" class="btnStyle" :loading="loading" @click="handleSubmit">确 定</el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data(){
    return {
      checkedimagesList: [],//已选中
      imagesList: [],//图片列表
      searchImg: '',//搜索图片
      dialogTableVisible: this.$store.state.batchImgStatus,
      imgOptionVal: '',
      currentPage: 1,
      pagerCount: 5,
      imgOption: [],//图库分类
      imgList: [],//图片列表
      total: 0,
      batchSelectIdData: '',
      loading: false,
    }
  },
  methods: {
    handleCheckedimagesListChange(value) {
      this.checkedimagesList = value;
    },
    handleCurrentChange(value){
      this.imgInitData('',this.searchImg,value);
    },
    //关闭批量选择图片接口
    handleClose(){
      this.loading = false;
      this.$store.commit('CHANGE_BATCH_IMG',false);
      this.checkedimagesList = [];
    },
    //搜索图片
    searchImgMethod(){
      this.imgInitData('',this.searchImg,1);
    },
    //获取图库分类列表
    async getClassification(){
      let res = await this.api.designGalleryCates();
      this.imgOption = res.data.data;
    },
    //图片分类改变
    handleChangeImg(value){
      this.imgInitData(value,this.searchImg,1);
    },
    //图片初始化
    imgInitData(cate_id,name,page){
      this.postRequest(`/v1/designGalleryLists?cate_id=${cate_id}&gallery_name=${name}&page=${page}`)
      .then(res => {
        if(res.data.code=='200'){
          this.total = res.data.data.total;
          this.imagesList = res.data.data.data;
        }
      })
      .catch(error => {
      })
    },
    //批量设计提交
    handleSubmit(){
      this.loading = true;
      let imgId = [];
      for (let index = 0; index < this.checkedimagesList.length; index++) {
        const element = this.checkedimagesList[index].id;
        imgId.push(element);
      }
      // 
      if(this.batchSelectIdData==''){
        this.handleClose();
      }else {
        this.postRequest(`/v1/batchImgMerge?goods_id=${this.batchSelectIdData}&images=${imgId.join(',')}`)
        .then(res => {
          this.handleClose();
          this.$store.commit('IMAGES_LIST_REFRESH');
          if(res.data.code=='200'){
            this.$message({
              type: 'success',
              message: '成功'
            })
          }
          else if (res.data.code=='202'){
            this.$message({
              type: 'warning',
              message: '请选择一种商品进行设计'
            })
          }else {
            this.$message.error('批量合成失败')
          }
        })
      }
    }
  },
  created(){
    this.getClassification();
  },
  computed: {
    batchImgStatus(){
      return this.$store.state.batchImgStatus;
    },
    batchSelectId(){
      return this.$store.state.batchSelectId;
    }
  },
  watch: {
    batchImgStatus: function(newVal,oldVal){
      this.dialogTableVisible = newVal;
    },
    batchSelectId: function(newVal,oldVal){
      this.batchSelectIdData = newVal;
    },
  }
}
</script>
<style lang="scss" scoped>
.imgInfo {
  background-color: #fff;
  padding: 5px;
  margin-bottom: 20px;
  vertical-align: top;
  margin-right: 10px;
  display: inline-block;
  // z-index: -1;
  img {
    width: 60px;
  }
}
</style>
