    // 删除变体价格
    async deleteVatiantPrice(item) {
      var requestData = {};
      this.$set(requestData, "vatiantList", item);
      this.$set(requestData, "goods_id", this.amazonExportTemInfoForm.ids);
      let res = await this.api.variationPricesettingDelete(requestData);
      if (res.data.code == "200") {
        this.$message({
          type: "success",
          message: "删除变种价格设定成功",
          offset: 100,
        });
        this.getVariantPriceSettingList();
      } else {
