          message: "删除变种价格设定失败",
          offset: 100,
        });
      }
    },
    // 修改变体价格
    editVariantPrice(item) {
      // 改变变体价格设定状态为修改
      this.variantPriceStatus = "edit";
      // 修改的时候给输入框赋值选中的价格变体信息
      this.variation_price_setting.price = item.price;
      this.variation_price_setting.template_product_id =
        item.template_product_id;
      this.variation_price_setting.id = item.id;
      this.variation_price_setting.temp_rand = item.temp_rand;
      if (item.size !== "") {
        this.variation_price_setting.size = item.size.split(",");
      } else {
        this.variation_price_setting.size.length = 0;
      }
      if (item.color !== "") {
        this.variation_price_setting.color = item.color.split(",");
      } else {
        this.variation_price_setting.color.length = 0;
      }
      if (this.variation_price_setting.size.length == this.sizeArr.length) {
        if (this.sizeArr.length == 1) {
          if (this.variation_price_setting.size[0] == this.sizeArr[0]) {
            this.checkedAllSize = true;
          } else {
            this.checkedAllSize = false;
          }
        } else {
          this.checkedAllSize = true;
        }
      } else {
        this.checkedAllSize = false;
      }
      if (this.variation_price_setting.color.length == this.colorArr.length) {
        if (this.variation_price_setting.color[0] == this.colorArr[0]) {
          this.checkedAllColor = true;
        } else {
          this.checkedAllColor = false;
        }
      } else {
        this.checkedAllColor = false;
      }
      this.priceSettingVisible = true;
    },
    // 提交变体价格设定
    async VariantPriceSubmit() {
      // 如果是添加价格变体时  id和随机数为空
      if (this.variantPriceStatus !== "edit") {
        this.variation_price_setting_two.id = "";
        this.variation_price_setting_two.temp_rand = "";
      } else {
        this.variation_price_setting_two.id = this.variation_price_setting.id;
        this.variation_price_setting_two.temp_rand = this.variation_price_setting.temp_rand;
      }
      // 把所选中的颜色尺码都传过去  循环
      this.variation_price_setting_two.color = [];
      this.variation_price_setting_two.size = [];
      for (var i = 0; i < this.variation_price_setting.color.length; i++) {
        for (var j = 0; j < this.priceSettingColor.length; j++) {
          if (
            this.variation_price_setting.color[i] ===
            this.priceSettingColor[j].color_name
          ) {
            this.variation_price_setting_two.color.push(
              this.priceSettingColor[j]
            );
          }
        }
      }
      for (var i = 0; i < this.variation_price_setting.size.length; i++) {
        for (var j = 0; j < this.priceSettingSize.length; j++) {
          if (
            this.variation_price_setting.size[i] ==
            this.priceSettingSize[j].size_name
          ) {
            this.variation_price_setting_two.size.push(
              this.priceSettingSize[j]
            );
          }
        }
      }
      // this.variation_price_setting_two.color = this.variation_price_setting.color.join(',')
      // this.variation_price_setting_two.size = this.variation_price_setting.size.join(',')
      this.variation_price_setting_two.price = this.variation_price_setting.price;
      this.variation_price_setting_two.template_product_id = this.amazonExportTemInfoForm.id;
      this.variation_price_setting_two.goods_id = this.amazonExportTemInfoForm.ids;

      if (this.type == "1") {
        if (this.variation_price_setting_two.size == "") {
          this.$message({
            type: "error",
            message: "请选择尺码",
            offset: 100,
          });
          return;
        }
        this.variation_price_setting.color.length = 0;
        this.variation_price_setting_two.color = [];
      } else if (this.type == "2") {
        if (this.variation_price_setting_two.color == []) {
          this.$message({
            type: "error",
            message: "请选择颜色",
            offset: 100,
          });
          return;
        }
        this.variation_price_setting.size.length = 0;
        this.variation_price_setting_two.size = [];
      } else {
        if (this.variation_price_setting_two.color == []) {
          this.$message({
            type: "error",
            message: "请选择颜色",
            offset: 100,
          });
          return;
        }
        if (this.variation_price_setting_two.size == []) {
          this.$message({
            type: "error",
            message: "请选择尺码",
            offset: 100,
          });
          return;
        }
      }
      if (this.variation_price_setting_two.price == "") {
        this.$message({
          type: "error",
          message: "请输入价格",
          offset: 100,
        });
        return;
      }
      let res = await this.api.variationPricesetting(
        this.variation_price_setting_two
      );
      if (res.data.code == "200") {
        this.priceSettingVisible = false;
        this.variantPriceStatus = "add";
        this.getVariantPriceSettingList();
      } else {
        this.priceSettingVisible = false;
        if (this.variantPriceStatus == "add") {
          this.error("添加价格变体失败");
        }
        if (this.variantPriceStatus == "edit") {
          this.error("修改价格变体失败");
        }
      }
    },
