    handleSelectSize(index, selectSizeArr, allSizeArr) {
      // 如果选中尺码数组长度=所有尺码数组的长度  就表示选中了全部尺码选项
      // 这样全选按钮就要为选中状态
      // 否则不选中
      if (selectSizeArr.length == allSizeArr.length) {
        this.selectedcheckedAllSize["size" + index] = true;
      } else {
        this.selectedcheckedAllSize["size" + index] = false;
      }
    },
    handleSelectedCheckAllChangeColor(index, val, allColorArr) {
      // arr 为所有颜色id数组
      var arr = [];
      for (var i = 0; i < allColorArr.length; i++) {
        arr.push(allColorArr[i].color_id);
      }
      //  如果val为true  就表示点击全选按钮  那么选中的颜色数组 = arr
      if (val) {
        this.sizeColorExportTable[index].select_color = arr;
      } else {
        this.sizeColorExportTable[index].select_color = [];
      }
    },
    handleSelectedCheckAllChangeSize(index, val, allSizeArr) {
      // arr 为所有尺码id数组
      var arr = [];
      for (var i = 0; i < allSizeArr.length; i++) {
        arr.push(allSizeArr[i].size_id);
      }
      // val为全选按钮的选中状态 如果val为true  就表示点击全选按钮  那么选中的尺码数组 = arr
      if (val) {
        this.sizeColorExportTable[index].select_size = arr;
      } else {
        this.sizeColorExportTable[index].select_size = [];
      }
    },
