    // 选择模板之后，获取模板详细信息，下一步汇出
    async saveExport() {
      // 先效果图数组  以免重复
      this.fileList.length = 0;
      // 改变指定颜色尺码汇出文字和图标  和指定颜色尺码汇出盒子状态
      this.isPlus = true;
      this.productBoxText = "指定颜色尺码汇出";
      this.isProductBox = false;
      // 获取模板信息
      let res = await this.api.templateDetail({
        id: this.amazonExportForm.export_template,
        goods_id: this.selectedId,
      });
      this.amazonExportOrderStatus = false;
      this.autoSaveExportLoading = false;
      this.amazonExportTemInfoStatus = true;
      // 获取国家列表
      this.getCountryList();
      this.amazonExportForm.store_name = "";
      this.amazonExportForm.export_template = "";
      if (res.data.code == "200") {
        this.productTypeList = res.data.data.productTypeList;

        // 复制亚马逊模板信息表单内容
        this.amazonExportTemInfoForm = res.data.data;
        // 如果颜色尺码名称自定义规则为null 咋不能输入  所以改为空
        if (this.amazonExportTemInfoForm.colorNameRegular == null) {
          this.amazonExportTemInfoForm.colorNameRegular = "";
        }
        this.allsizeExportTable = res.data.data.all_size;
        this.allcolorExportTable = res.data.data.all_color;
        this.aliasSizeArr = res.data.data.alias_size;
        this.aliasColorArr = res.data.data.alias_color;
        for (var i = 0; i < this.allsizeExportTable.length; i++) {
          this.$set(this.allsizeExportTable[i], "alias_name", "");
          for (var j = 0; j < this.aliasSizeArr.length; j++) {
            if (
              this.aliasSizeArr[j].select_size_id ==
              this.allsizeExportTable[i].size_id
            ) {
              this.allsizeExportTable[i].alias_name = this.aliasSizeArr[
                j
              ].alias_size;
            }
          }
        }
        for (var i = 0; i < this.allcolorExportTable.length; i++) {
          this.$set(this.allcolorExportTable[i], "alias_name", "");
          for (var j = 0; j < this.aliasColorArr.length; j++) {
            if (
              this.aliasColorArr[j].select_color_id ==
              this.allcolorExportTable[i].color_id
            ) {
              this.allcolorExportTable[i].alias_name = this.aliasColorArr[
                j
              ].alias_color;
            }
          }
        }
        // 赋值尺码颜色数组  自定义颜色尺码要用
        this.sizeColorExportTable = res.data.data.goods_info;
        // 因为是循环  所以每个产品颜色尺码全选按钮状态的绑定值要根据产品生成一个对象进行绑定
        // 不能用数组
        for (var i = 0; i < this.sizeColorExportTable.length; i++) {
          this.$set(this.selectedcheckedAllColor, "color" + i, false);
        }
        for (var i = 0; i < this.sizeColorExportTable.length; i++) {
          this.$set(this.selectedcheckedAllSize, "size" + i, false);
        }
        // 赋值价格设定尺码数组
        // this.priceSettingSize = this.sizeColorExportTable[0].size
        this.priceSettingSize = res.data.data.all_size;
        for (var i = 0; i < this.priceSettingSize.length; i++) {
          this.sizeArr.push(this.priceSettingSize[i].size_name);
        }
        // 赋值价格设定颜色数组
        // this.priceSettingColor = this.sizeColorExportTable[0].color
        this.priceSettingColor = res.data.data.all_color;
        // 循环生成价格变体设定所有颜色名称数组
        for (var i = 0; i < this.priceSettingColor.length; i++) {
          this.colorArr.push(this.priceSettingColor[i].color_name);
        }
        // 在提交模板信息中添加亚马逊汇出产品id
        this.exportProId = this.selectedId.join(",");
        this.$set(this.amazonExportTemInfoForm, "ids", this.exportProId);
        // 设置产品效果图
        for (var i = 0; i < this.amazonExportTemInfoForm.imgs.length; i++) {
          if (i == 0) {
            if (this.amazonExportTemInfoForm.imgs[0].type == "1") {
              this.fileList.push({
                index: this.amazonExportTemInfoForm.imgs[0].main_image_url,
                type: this.amazonExportTemInfoForm.imgs[0].type,
                url: "",
              });
            } else {
              this.fileList.push({
                index: this.amazonExportTemInfoForm.imgs[0].main_image_url,
                type: this.amazonExportTemInfoForm.imgs[0].type,
                url: this.amazonExportTemInfoForm.imgs[0].main_image_url,
              });
            }
          } else {
            if (this.amazonExportTemInfoForm.imgs[i].type == "1") {
              this.fileList.push({
                index: this.amazonExportTemInfoForm.imgs[i][
                  "other_image_url" + i
                ],
                type: this.amazonExportTemInfoForm.imgs[i].type,
                url: "",
              });
            } else {
              this.fileList.push({
                index: this.amazonExportTemInfoForm.imgs[i][
                  "other_image_url" + i
                ],
                type: this.amazonExportTemInfoForm.imgs[i].type,
                url: this.amazonExportTemInfoForm.imgs[i][
                  "other_image_url" + i
                ],
              });
            }
          }
        }
        // console.log(this.fileList);

        // 必填信息的变量名称
        this.productVaribles = this.amazonExportTemInfoForm.data;
        // 必填信息的变量信息  同上有差别
        // 循环两个数组 完善productVariable
        this.varibleInfo = this.amazonExportTemInfoForm.excel_message;
        for (var i = 0; i < this.varibleInfo.length; i++) {
          if (this.varibleInfo[i][0].name == "Required") {
            for (var a = 0; a < this.varibleInfo[i].length; a++) {
              for (var s = 0; s < this.productVaribles.length; s++) {
                if (this.productVaribles[s].name == "Required") {
                  for (var y = 0; y < this.productVaribles[s].sub.length; y++) {
                    if (
                      this.productVaribles[s].sub[y].name ==
                      this.varibleInfo[i][a].name
                    ) {
                      this.$set(
                        this.productVaribles[s].sub[y],
                        "type",
                        this.varibleInfo[i][a].type
                      );
                    }
                  }
                }
              }
            }
          }
          for (var j = 0; j < this.varibleInfo[i].length; j++) {
            if (this.varibleInfo[i][j].params) {
              for (var k = 0; k < this.productVaribles.length; k++) {
                for (var n = 0; n < this.productVaribles[k].sub.length; n++) {
                  if (
                    this.productVaribles[k].sub[n].name ==
                    this.varibleInfo[i][j].name
                  ) {
                    this.productVaribles[k].sub[n].params = this.varibleInfo[i][
                      j
                    ].params;
                  }
                }
              }
            }
          }
        }
        for (var i = 0; i < this.sizeColorExportTable.length; i++) {
          // 每次进入模板信息时 指定颜色尺码信息不保留  重新选择  所以要清空选中颜色尺码数组
          if (this.sizeColorExportTable[i].select_size.length !== 0) {
            this.sizeColorExportTable[i].select_size.length = 0;
          }
          if (this.sizeColorExportTable[i].select_color.length !== 0) {
            this.sizeColorExportTable[i].select_color.length = 0;
          }
          // 给size数组里边添加别名alias_size属性
          for (var j = 0; j < this.sizeColorExportTable[i].size.length; j++) {
            this.$set(this.sizeColorExportTable[i].size[j], "alias_size", "");
            for (
              var k = 0;
              k < this.sizeColorExportTable[i].alias_size.length;
              k++
            ) {
              if (
                this.sizeColorExportTable[i].size[j].size_name ==
                this.sizeColorExportTable[i].alias_size[k].select_name
              ) {
                this.$set(
                  this.sizeColorExportTable[i].size[j],
                  "alias_size",
                  this.sizeColorExportTable[i].alias_size[k].alias_size
                );
              }
            }
          }
          // 给color数组里边添加别名alias_color属性
          for (var j = 0; j < this.sizeColorExportTable[i].color.length; j++) {
            this.$set(this.sizeColorExportTable[i].color[j], "alias_color", "");
            for (
              var k = 0;
              k < this.sizeColorExportTable[i].alias_color.length;
              k++
            ) {
              if (
                this.sizeColorExportTable[i].color[j].color_name ==
                this.sizeColorExportTable[i].alias_color[k].select_name
              ) {
                this.$set(
                  this.sizeColorExportTable[i].color[j],
                  "alias_color",
                  this.sizeColorExportTable[i].alias_color[k].alias_color
                );
              }
            }
          }
        }
        // 获取变体价格设定列表
        this.getVariantPriceSettingList();
      }
    },
    // 确定保存并汇出，下一步调用保存并汇出接口
    saveConfirm() {
      let _this = this;
      if (_this.imageList.length == 0) {
        this.submit();
      } else {
        let formData = new FormData();
        formData.append("files", this.files);
        for (let index = 0; index < _this.imageList.length; index++) {
          const element = _this.imageList[index].raw;
          formData.append("image[]", element);
        }
        this.imgRequest("/v1/upLoadimgs", formData)
          .then((res) => {
            if (res.data.code == 200) {
              if (this.amazonExportTemInfoForm.imgs !== "") {
                this.amazonExportTemInfoForm.imgs =
                  this.amazonExportTemInfoForm.imgs + "," + res.data.data;
              } else {
                this.amazonExportTemInfoForm.imgs = res.data.data;
              }
              this.submit();
            }
          })
          .catch((error) => {
            this.saveDisabled = false;
          });
      }
    },
    // 调用保存并汇出接口
    async submit() {
      this.amazonExportTemInfoForm.variantPriceList = this.variantPriceList;
      for (var i = 0; i < this.amazonExportTemInfoForm.data.length; i++) {
        // 提交时  提交内容为无改成空
        if (this.amazonExportTemInfoForm.data[i].name !== "Required") {
          for (
            var j = 0;
            j < this.amazonExportTemInfoForm.data[i].sub.length;
            j++
          ) {
            if (this.amazonExportTemInfoForm.data[i].sub[j].values == "无") {
              this.amazonExportTemInfoForm.data[i].sub[j].values = "";
            }
          }
        }
        // 必选属性不能为空
        if (this.amazonExportTemInfoForm.data[i].name == "Required") {
          for (
            var j = 0;
            j < this.amazonExportTemInfoForm.data[i].sub.length;
            j++
          ) {
            if (
              this.amazonExportTemInfoForm.data[i].sub[j].values == "" &&
              this.amazonExportTemInfoForm.data[i].sub[j].type == "Required" &&
              this.amazonExportTemInfoForm.data[i].sub[j].name !==
                "main_image_url"
            ) {
              var name = this.amazonExportTemInfoForm.data[i].sub[j].name;
              this.$message({
                type: "error",
                offset: 300,
                message: name + "不能为空",
              });
              return;
            }
            if (
              this.amazonExportTemInfoForm.data[i].sub[j].values == "无" &&
              this.amazonExportTemInfoForm.data[i].sub[j].name !==
                "main_image_url"
            ) {
              var name = this.amazonExportTemInfoForm.data[i].sub[j].name;
              this.$message({
                type: "error",
                offset: 300,
                message: name + "不能为空",
              });
              return;
            }
          }
        }
      }
      this.exportLoading = true;
      this.amazonExportTemInfoForm.imgs = [];
      // 设置效果图主图和其他
      for (var i = 0; i < this.fileList.length; i++) {
        var obj = {};
        if (i == 0) {
          if (this.fileList[0].type == "1") {
            this.$set(obj, "main_image_url", this.fileList[0].index);
          } else {
            this.$set(obj, "main_image_url", this.fileList[0].url);
          }
          this.$set(obj, "type", this.fileList[0].type);
          this.$set(
            obj,
            "key",
            this.fileList[0].index.substr(this.fileList[0].index.length - 1, 1)
          );
        } else {
          if (this.fileList[i].type == "1") {
            this.$set(obj, "other_image_url" + i, this.fileList[i].index);
          } else {
            this.$set(obj, "other_image_url" + i, this.fileList[i].url);
          }
          this.$set(obj, "type", this.fileList[i].type);
          this.$set(
            obj,
            "key",
            this.fileList[i].index.substr(this.fileList[i].index.length - 1, 1)
          );
        }
        this.amazonExportTemInfoForm.imgs.push(obj);
      }
      // 如果售卖形式不为产品变体  合并数量和颜色名称自定义规则为空
      if (this.amazonExportTemInfoForm.sales_type !== "4") {
        this.amazonExportTemInfoForm.colorNameRegular = "";
        this.amazonExportTemInfoForm.concatNumber = "";
      }
      let u_id = sessionStorage.getItem("selectUId");
      // 指定颜色尺码汇出是如果没选择汇出是就选择全部颜色或尺码
      for (var i = 0; i < this.sizeColorExportTable.length; i++) {
        var colorArr = [];
        var sizeArr = [];
        for (var j = 0; j < this.sizeColorExportTable[i].color.length; j++) {
          colorArr.push(this.sizeColorExportTable[i].color[j].color_id);
        }
        for (var k = 0; k < this.sizeColorExportTable[i].size.length; k++) {
          sizeArr.push(this.sizeColorExportTable[i].size[k].size_id);
        }
        if (this.sizeColorExportTable[i].select_color.length == 0) {
          this.sizeColorExportTable[i].select_color = colorArr;
        }
        if (this.sizeColorExportTable[i].select_size.length == 0) {
          this.sizeColorExportTable[i].select_size = sizeArr;
        }
      }
      let res = await this.api
        .ExcelSave(this.amazonExportTemInfoForm)
        // this.postLogisticsData(`/v1/ExcelSave?u_id=${u_id}`, this.amazonExportTemInfoForm)
        .then((res) => {
          // console.log(res.data.code);
          if (res.data.code == "200") {
            this.exportLoading = false;
            this.dialogexportGoodsExcelVisible = true;
            // console.log("ddddddddddddd");
            /* this.$message({
              dangerouslyUseHTMLString: true,
              message: '<router-link  :to="{path:\"/login\"}" >汇出成功</router-link>汇出成功,请前往 <a href="exportRecord">汇出记录</a>查看',
              type: 'success'
            });*/
            this.amazonExportTemInfoStatus = false;
            // window.location.href = res.data.data
          } else {
            this.exportLoading = false;
            this.$message({
              message: "汇出失败",
              type: "error",
            });
            this.amazonExportTemInfoStatus = false;
          }
        })
        .catch((error) => {
          this.exportLoading = false;
          this.$message({
            message: "汇出失败",
            type: "error",
          });
        });
    },
