    //滚动查看图片
    wheel(imgValue, e = event) {
      this.bodyMouseWheel(e);
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if (imgValue.length >= imgIndex && imgIndex >= 0) {
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if (imgIndex == -1) {
          imgIndex = imgValue.length - 1;
          // imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if (imgValue.length > imgIndex) {
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
        }
        if (imgValue.length == imgIndex) {
          imgIndex = 0;
        }
      }
    },
    mouseover(val, index = 0) {
      this.bigImgSrc = val[index].path;
    },
    bodyMouseWheel(e) {
      e.preventDefault();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    //设计
    handleClickDesgin() {
      let mainGoodsId = [];
      for (let index = 0; index < this.multipleSelection.length; index++) {
        const element = this.multipleSelection[index];
        if (element.main_goods_id == 0) {
          mainGoodsId.push(element.id);
        }
      }
      if (mainGoodsId.length != 1) {
        this.$message({
          type: "warning",
          message: "请选择一样商品进行设计",
        });
      } else {
        this.dbClickDesign(mainGoodsId[0]);
      }
    },
    //批量设计图片
    // handleClickBatch(){
    //   // ;
    //   // if(this.multipleSelection.length==1){
    //     this.$store.commit('CHANGE_BATCH_IMG',true);
    //     let batchSelectId = [];
    //     for (let index = 0; index < this.multipleSelection.length; index++) {
    //       const element = this.multipleSelection[index];
    //       batchSelectId.push(element.id);
    //     }
    //     this.$store.commit('BATCH_SELECT_ID',batchSelectId.join(','));
    //   // }else {
    //   //   this.$message({
    //   //     type: 'warning',
    //   //     message: '只能选择一条记录进行批量设计'
    //   //   })
    //   // }
    // },
    handleClickBatch() {
      let mainGoodsId = [];
      for (let index = 0; index < this.multipleSelection.length; index++) {
        const element = this.multipleSelection[index];
        if (element.main_goods_id == 0) {
          mainGoodsId.push(element.id);
        }
      }
      if (mainGoodsId.length != 1) {
        this.$message({
          type: "warning",
          message: "请选择一样商品进行设计",
        });
      } else {
        // window.location.href = `http://perdesign.iypic.com?product=${mainGoodsId[0]}&userid=${sessionStorage.getItem('selectUId')}`;
        window.location.href = `http://design.321.design/index/batch/index/?product=${
          mainGoodsId[0]
        }&userid=${sessionStorage.getItem("selectUId")}`;
      }
    },
    //修改商品
    handleEditPro() {
      let mainGoodsId = [];
      for (let index = 0; index < this.multipleSelection.length; index++) {
        const element = this.multipleSelection[index];
        if (element.main_goods_id == 0) {
          mainGoodsId.push(element.id);
        }
      }
      if (mainGoodsId.length != 1) {
        this.$message({
          type: "warning",
          message: "请选择一样商品进行修改",
        });
      } else {
        this.dbClickRow(this.multipleSelection[0]);
      }
    },
    //处理传入数据的id，循环遍历出来
    handleId(data) {
      this.multipleSelectionId = [];
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          const element = data[key];
          this.multipleSelectionId.push(element.id);
        }
      }
    },
    //多选商品修改信息
    // handleSelectionChange(val) {
    //   this.multipleSelection = [];
    //   for (const key in val) {
    //     if (val.hasOwnProperty(key)) {
    //       const element = val[key];
    //       this.multipleSelection.push(element.id);
    //       for (const key in element.sub_goods) {
    //         if (element.sub_goods.hasOwnProperty(key)) {
    //           let id = element.sub_goods[key];
    //           this.multipleSelection.push(id.id);
    //         }
    //       }
    //     }
    //   }
    // },
    handleSelectionChange(val) {
      this.selectedId = [];
      for (let i = 0; i < val.length; i++) {
        this.selectedId.push(val[i].id);
      }
      this.multipleSelection = [];
      for (const key in val) {
        if (val.hasOwnProperty(key)) {
          const element = val[key];
          this.multipleSelection.push(element);
          for (const key in element.sub_goods) {
            if (element.sub_goods.hasOwnProperty(key)) {
              let id = element.sub_goods[key];
              this.multipleSelection.push(id);
            }
          }
        }
      }
    },
    //双击修改
    dbClickRow(val) {
      this.passValue.showState = true;
      this.ruleForm = val;
      if (val.main_goods_id == "0") {
        this.disabledStatus = true;
      } else {
        this.disabledStatus = false;
      }
      // this.$set(this.ruleForm,'main_goods_id',1);
      this.$set(this.ruleForm, "syn_status", 1);
      // ;
    },
    //双击设计
    dbClickDesign(id) {
      sessionStorage.setItem("desginGoodsId", id);
      window.location.href = `http://design.321.design/index/index/index/?goods=${id}&userid=${sessionStorage.getItem(
        "selectUId"
      )}`;
      // this.$router.push('/copyDesginPage');
      // this.$router.push('/DesginPage');
    },
    //双击table->row
    handledbClickRow(val) {
      let designState = sessionStorage.getItem("designState");
      if (designState == "true") {
        this.dbClickDesign(val.id);
      } else {
        this.dbClickRow(val);
      }
    },
    //关闭弹出框
    handleClose() {
      this.passValue.showState = false;
      (this.ruleForm = {
        goods_size: {
          color: "",
          size: "",
          goods_weight: "",
        },
      }),
        (this.imgToolShowStatus = false);
    },
    //步进器改变->发货周期
    //保存并退出修改的商品信息
    addThisProduct(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.$set(this.ruleForm, "sub_goods", "");
          // let formData = new FormData();
          // formData.append('ruleForm',JSON.stringify(this.ruleForm));
          let res = await this.api.goodsEdit(this.ruleForm);
          // this.postRequest(`v1/goodsEdit`,JSON.stringify(this.ruleForm))
          // this.imgRequest(`v1/goodsEdit`,formData)
          if (res.data.code == "200") {
            this.$message({
              type: "success",
              message: "修改成功",
            });
            this.handleClose();
            this.initData();
            // this.passValue.showState = false;
          }
        } else {
          return false;
        }
      });
    },
    uploadImg() {
      // this.imgRequest(`/v1/upLoadimgs`,)
    },
    // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists();
      if (res.data.code == 200) {
        this.storeList = res.data.data;
        this.amazonExportTemInfoForm.store_name = this.storeList[0].id;
      }
    },
  },
  created() {
    this.initData();
    this.getTreeData();
    this.getStoreList();
    this.getAccountList();
  },
  components: {
    buttonUploadFile,
    batchImg,
  },
  computed: {
    cateId() {
      return this.$store.state.shopManagementCate.goods_cate_id;
    },
    shopState() {
      return this.$store.state.shopManagementCate.status;
    },
    limitData() {
      return this.$store.state.shopManagementCate.limit;
    },
    searchValue() {
      return this.$store.state.shopManagementCate.goods_name;
    },
    shopProductCateId() {
      return this.$store.state.shopManagementCate.product_cate_id;
    },
    exportStatus() {
      return this.$store.state.shopManagementCate.exportStatus;
    },
    imgData() {
      return this.$store.state.imgData;
    },
    changeUserId() {
      return this.$store.state.changeUserId;
    },
    TransferredDirectoryStatus() {
      return this.$store.state.TransferredDirectoryStatus;
    },
    imgListRefresh() {
      return this.$store.state.imgListRefresh;
    },
    length() {
      return this.fileList.length;
    },
    goodsChecked() {
      return this.$store.state.goodsChecked;
    },
  },
  watch: {
    goodsChecked: function(newVal, oldVal) {
      if (newVal == "0") {
        this.checkedGoodsIds = [];
        this.multipleSelection = [];
        this.exportProId = "";
        this.amazonExportTemInfoForm.ids = "";
        this.selectedId = [];
      }
      if (newVal == "1") {
        this.checkedGoodsIds = this.tableData;
        this.multipleSelection = this.allGoodsIds;
        this.selectedId = this.allGoodsIds;
        this.exportProId = this.allGoodsIdsStr;
        this.amazonExportTemInfoForm.ids = this.allGoodsIdsStr;
      }
    },
    imgListRefresh: function(newVal, oldVal) {
      this.initData(sessionStorage.getItem("selectUId"));
    },
    cateId: function(newVal, oldVal) {
      //sessionStorage.setItem("oldShopCate_id", newVal); //防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData();
    },
    shopState: function(newVal, oldVal) {
      sessionStorage.setItem("shopState", newVal); //防止用户刷新页面时因为shopState被刷新掉而进行重复请求
      this.initData();
    },
    searchValue(newVal, oldVal) {
      this.initData();
    },
    shopProductCateId(newVal, oldVal) {
      sessionStorage.setItem("product_cate_id", newVal); //防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData();
    },
    limitData(newVal, oldVal) {
      this.initData();
    },
    exportStatus(newVal, oldVal) {
      sessionStorage.setItem("product_export_status", newVal); //防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData();
    },
    imgData(newVal, oldVal) {
      this.uploadImgData = newVal;
      this.addCurrentLocationImg();
    },
    changeUserId: function(newVal, oldVal) {
      this.initData();
      this.getTreeData();
    },
    TransferredDirectoryStatus: function(newVal, oldVal) {
      this.getTreeData();
      // this.initData();
    },
  },
};
