<template>
  <el-upload
    class="upload-demo"
    :action="uploadFileData.uploadSrc"
    multiple
    :limit="1"
    :auto-upload="false"
    :on-change="handleChange"
    :file-list="fileList">
    <el-button size="small" class="width100">当前位置后添加</el-button>
  </el-upload>
  
</template>
<script>
  export default {
    props: ['uploadFileData'],
    data() {
      return {
        fileList: []
      };
    },
    methods: {
      handleSuccess(){
      },
      handleChange(file, fileList){
        this.$store.commit('CHANGE_IMG_DATA',file);
      },
    }
  }
</script>
